.btn2-anim {
  position: relative;
  width: auto;
  padding: 0 19px;
  transition: all 0.5s ease-out 1.8s;
  height: 44px;
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn2-anim:after {
  content: "";
  position: absolute;
  left: 0px;
  top: 0px;
  width: 44px;
  height: 100%;
  border-radius: 30px;
  background: #000;
  opacity: 0.2;
  transition: all 0.45s ease-in-out 0s;
}
.btn2-anim:hover::after {
  width: 100%;
  opacity: 0.8;
}
.btn2-anim span {
  font-weight: bold;
  position: relative;
  display: block;
  padding-right: 41px;
  background: url(../img/arrw_right_04.png) right center no-repeat;
  font-size: 15px;
  line-height: 33px;
  color: #fff;
  z-index: 100;
  white-space: nowrap;
}

.arck-project-item:hover .btn2-anim:after {
  width: 100%;
  opacity: 0.8;
}

.arck-project-item-overlay .inner-img:after {
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  content: "";
  height: 100%;
  position: absolute;
  transition: var(--transition);
  background-color: rgba(0, 0, 0, 0.2);
}

